<template>
  <div class="mapwrapper">

      {{address}}
      {{markers}}
      {{center}}
      

      <el-amap vid="amapDemo" :zoom="zoom"  :center="center" class="amap-demo" :plugin="plugin" :lang="'zh_en'">
       
             <el-amap-marker v-for="(marker, index) in markers" :position="marker.position" :key="index" :content="marker.content"></el-amap-marker>


      </el-amap>

  </div>
</template>

<script>
// import VueAMap from 'vue-amap'
export default {
  props:['address'],
  created(){
    this.getLnglat()
  },
  methods:{
    getLnglat(){

      let address = this.address
      let markers = this.markers
      let center = this.center
      // let amapManager = new VueAMap.AMapManager()

      AMap.service('AMap.Geocoder',function(){//回调函数
      //实例化Geocoder
        let geocoder = new AMap.Geocoder({
          city: "021"//城市，默认：“全国”
        })
      //TODO: 使用geocoder 对象完成相关功能
            geocoder.getLocation(address, function(status, result) {
          if (status === 'complete' && result.info === 'OK') {
            console.log(result)
              //TODO:获得了有效经纬度，可以做一些展示工作
              //比如在获得的经纬度上打上一个Marker
            let lat =  result.geocodes[0].location.lat
            let lng =  result.geocodes[0].location.lng

            center = [lng,lat]
            
              markers.push({
                position:[lng,lat],
                content:`🐷`
              })
             
          }else{
              //获取经纬度失败
              console.log('failed')
          }
          })
      })
      
    }
  },
  data () {
    let self = this
    return {
        zoom: 15,
        center: [121.5273285, 31.21515044],
        lng: 0,
        lat: 0,
        loaded: false,
        markers: [],
        
        plugin: [
          {
            pName: 'ToolBar',
            events: {
              init(instance) {
              }
            }
          },
          {
          pName: 'Geolocation',
          events: {
            // init(o) {
            //   // o 是高德地图定位插件实例
            //   o.getCurrentPosition((status, result) => {
            //     if (result && result.position) {
            //       self.lng = result.position.lng;
            //       self.lat = result.position.lat;
            //       self.center = [self.lng, self.lat];
            //       self.loaded = true;
            //       self.$nextTick();
            //     }
            //   });
            // }
          }
        }]
    }
  },
  components : {

  }
}
</script>

<style lang="stylus" scoped>
.mapwrapper
  width 100%
  height 100%

</style>    




