<template>
  <div class="room-details-wrapper">
    <yd-navbar  :title="roominfo.district">
        <router-link to="/" slot="left">
            <yd-navbar-back-icon>Back</yd-navbar-back-icon>
        </router-link>

         <router-link to="/saved-apartments" slot="right">
                <span>Favorite</span>
                <yd-icon name="shopcart-outline" color="#FF685D" size="30px"></yd-icon>
                <yd-badge  type="danger">{{ this.$store.state.favlist.length }}</yd-badge>
            </router-link>
    </yd-navbar>



<el-row :gutter="20" class="roominfo">
    <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">    
        <yd-slider autoplay="3000">
            <yd-slider-item v-for="item,index in aptimgs" :key="index">            
                    <img :src="item">
            </yd-slider-item>
        </yd-slider>
    </el-col>

  
    <el-col :xs="24" :sm="12" :md="12" :lg="12" :xl="12">   
      <div class="desc">
        

          <div class="bigtitle">{{ roominfo.rooms_num + ' Bdr Apartment in ' + roominfo.district + ' rent ' + roominfo.price}}</div>


          <yd-cell-group>
            <yd-cell-item>
                <span slot="left">    <yd-badge shape="square" type="hollow">District: {{roominfo.district}}</yd-badge></span>
                <span slot="right">Feature Tags: {{roominfo.facilities}}</span>
            </yd-cell-item>
            
        </yd-cell-group>

    <yd-grids-group :rows="2">
        <yd-grids-item>
            <span slot="text">Rent: {{roominfo.price}}</span>
        </yd-grids-item>
        <yd-grids-item>
            <span slot="text">Compound: {{roominfo.compound}}</span>
        </yd-grids-item>
    </yd-grids-group>

    <yd-grids-group :rows="2">
        <yd-grids-item>
            <span slot="text">Approx. Sqms: {{roominfo.space}}</span>
        </yd-grids-item>
        <yd-grids-item>
            <span slot="text">Approx. Sqms: 15</span>
        </yd-grids-item>
    </yd-grids-group>

     <yd-grids-group :rows="2">
        <yd-grids-item>
            <span slot="text">Type: {{roominfo.type}}</span>
        </yd-grids-item>
        <yd-grids-item>
            <span slot="text">Stories: {{roominfo.floor}}</span>
        </yd-grids-item>
    </yd-grids-group>
          
      </div>
    </el-col>

    <el-row>
  
  
    <el-button @click.native="addToFav" type="success"  plain class="addtofav">
      Add on your favorite list 
    </el-button>

       
 
    </el-row>
  
</el-row>


    <div class="map">     
        <DetailMap :address="roominfo.address"></DetailMap>
    </div>
    
  </div>
</template>
<script>
import DetailMap from '@/page/listing/detail-map'
import {APIGetroom} from '../../api/api'
import {mapMutations} from 'vuex'
export default {
  components:{
      DetailMap,
  },
  data () {
      return {
          roominfo:{},
          crawedimage:'',
          added:false,
          roomimg:[''],
          roomid:this.$route.params.id,
      }
  },
  methods: {

      addToFav(){
        this.$store.dispatch('addFavList',this.roominfo)
        this.$dialog.notify({
                    mes: 'You have sucessed add this item on your list',
                    timeout: 2000,
                    callback: () => {
                        console.log('我走咯！');
                    }
                })
      },
      Showroomdetails(){
          APIGetroom(
              this.$route.params.id
          ).then( res => {
              this.roominfo = res.data
              if (res.data.crawedimage){
                this.crawedimage = res.data.crawedimage
              }else{
                this.crawedimage = 'No Image,,,No Image'
              }
              
            console.log(res.data)
          }).catch( err => { console.log(err) })
      },
      Showaddbtn(){
          console.log(this.added)
          let record = this.$store.state.favlist.find( n => n.id == this.roomid )
          if (record){
              this.added = false
              console.log(this.added)
          }else{
              this.added = true
          }
      }

  },
  computed:{
      aptimgs: function () {
          return this.crawedimage.split(',,,')
      }
  },
  created(){
      this.Showaddbtn()
  },
  mounted(){
      this.Showroomdetails()

  }
}
</script>
<style lang="stylus" scoped>
.room-details-wrapper
    .addtofav
        margin-top 20px
    .roominfo
        margin-top 20px
    .map
        width 100%
        height 300px
        margin-top 20px
</style>





